import React, { useEffect } from "react";
import { Route, Routes, BrowserRouter as Router } from "react-router-dom";
import "./App.css";
import Caraousel from "./component/Caraousel";
import ContactUs from "./component/ContactUs";
import Footer from "./component/Footer";
import Main from "./component/Main";
import NavBar from "./component/NavBar";
import Login from "./component/Login";
import Register from "./component/Register";
import Maintenance from "./component/Maintenance";
import Hospital from "./component/services/Hospital";
import Food from "./component/services/Food";
import Bed from "./component/services/Bed";
import Medicine from "./component/services/Medicine";
import Plasma from "./component/services/Plasma";
import Volunteer from "./component/services/Volunteer";
import Oxygen from "./component/services/Oxygen";
import About from "./component/About";
import Welcome from "./component/private/Welcome";
import Mainservice from "./component/Mainservice";
import Team from "./component/tfc/team";
import Logout from "./component/Logout";
import { ProvideAuth } from "./Utils/useAuth";
import DataDashBoard from "./component/DataDashboard";
import { ToastContainer } from "react-toastify";
import JitsiMeetComponent from "./component/DataDashboard/Chat/jitsi";

const App = () => {
  return (
    <div>
      <ToastContainer />
      <Router>
        <ProvideAuth>
          <Routes>
            <Route
              exact
              path="/"
              element={
                <>
                  <NavBar />
                  <Maintenance />
                  <Main />
                  <Mainservice />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/datadashboard"
              element={
                <>
                  <NavBar />
                  <DataDashBoard />
                </>
              }
            />

            <Route
              exact
              path="/oxygen"
              element={
                <>
                  <NavBar />
                  <Oxygen />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/hospital"
              element={
                <>
                  <NavBar />
                  <Hospital />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/beds"
              element={
                <>
                  <NavBar />
                  <Bed />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/food"
              element={
                <>
                  <NavBar />
                  <Food />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/medicine"
              element={
                <>
                  <NavBar />
                  <Medicine />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/plasma"
              element={
                <>
                  <NavBar />
                  <Plasma />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/volunteer"
              element={
                <>
                  <NavBar />
                  <Volunteer />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/login"
              element={
                <>
                  <NavBar />
                  <Login />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/register"
              element={
                <>
                  <NavBar />
                  <Register />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/contacts"
              element={
                <>
                  <NavBar />
                  <ContactUs />
                  <Caraousel />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/about"
              element={
                <>
                  <NavBar />
                  <About />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/team"
              element={
                <>
                  <NavBar />
                  <Team />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />

            <Route
              exact
              path="/welcome"
              element={
                <>
                  <NavBar />
                  <Welcome />
                  <Caraousel />
                  <ContactUs />
                  <Footer />
                </>
              }
            />
            <Route
              exact
              path="/videocall/:id"
              element={
                <>
                  <JitsiMeetComponent />
                </>
              }
            />

            <Route
              exact
              path="/logout"
              element={
                <>
                  <Logout />
                </>
              }
            />
          </Routes>
        </ProvideAuth>
      </Router>
    </div>
  );
};

export default App;
