import React, { useState, useEffect, useRef } from "react";
import { useNavigate, useLocation } from "react-router-dom";
// import menu from "../../../Assets/Images/icons/menu.png";
import SearchIcon from "@mui/icons-material/Search";
import "./messages.css";
import AttachFileIcon from "@mui/icons-material/AttachFile";
import ScrollableFeed from "react-scrollable-feed";
import Axios from "axios";

// import { useLocation } from "react-router-dom";
import { Avatar } from "@mui/material";

import FileDownloadIcon from "@mui/icons-material/FileDownload";
import PhotoIcon from "@mui/icons-material/Photo";
import { VideoCall } from "@mui/icons-material";
import MonitorHeartIcon from "@mui/icons-material/MonitorHeart";
import { toast } from "react-toastify";
import CircularProgress from "@mui/material/CircularProgress";
// import CancelIcon from "@mui/icons-material/Cancel";
import Popover from "@mui/material/Popover";
import Button from "@mui/material/Button";
import { useAuth } from "../../../Utils/useAuth";
import { io } from "socket.io-client";

export default function Messages() {
  const { user, setSessionExpire } = useAuth();
  const scrollRef = useRef();
  const socket = useRef();
  useEffect(() => {
    if (user) {
      socket.current = io(`http://localhost:5000/`);
      socket.current.emit("add-user", user._id);
    }
  }, [user]);

  const [selectedChat, setSelectedChat] = useState([]);
  const [recentChats, setRecentChats] = useState([]);
  const [openChatBox, setOpenChatBox] = useState(false);
  const [selectedChatId, setselectedChatId] = useState(null);
  const [currentReceiver, setCurrentReceiver] = useState(null);
  const [messageText, setmessageText] = useState("");

  const [sendingAttach, setsendingAttach] = useState(false);

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleAttachClose = () => {
    setAnchorEl(null);
  };

  const openAttach = Boolean(anchorEl);
  const id = openAttach ? "simple-popover" : undefined;
  const navigate = useNavigate();
  const getConversations = async () => {
    await Axios({
      method: "get",
      url: `/api/user/getchatrooms`,
    })
      .then((res) => {
        if (res.data.success) setRecentChats(res.data.chatrooms);
      })
      .catch((err) => {
        if (err.response && err.response.status === 401) {
          setSessionExpire(true);
        }
      });
  };

  useEffect(() => {
    getConversations();
  }, []);

  const search = useLocation().search;
  let chat_id = new URLSearchParams(search).get("id");

  useEffect(() => {
    if (chat_id) {
      handleChatSelect();
    }
  }, [chat_id]);

  const handleChatSelect = async () => {
    setOpenChatBox(true);
    setselectedChatId(chat_id);
    await Axios({
      method: "get",
      url: `/api/user/getchat/${chat_id}`,
    })
      .then((res) => {
        if (res.data.success) setSelectedChat(res.data.data);
      })
      .catch((err) => {
        if (err.response && err.response.status === 401) {
          setSessionExpire(true);
        }
      });
  };

  useEffect(() => {
    scrollRef.current?.scrollIntoView({
      behaviour: "smooth",
    });
  }, [selectedChat]);

  const sendAttachment = (e) => {
    const file = e.target.files[0];

    let type;
    if (file) {
      const fileInfo = file.name.split(".");
      type = fileInfo[1];
    }
    if (
      file &&
      type != "ppt" &&
      type != "pptx" &&
      type != "pdf" &&
      type != "docx" &&
      type != "doc" &&
      type != "xls" &&
      type != "xlsx" &&
      type != "html" &&
      type != "txt" &&
      type != "jpg" &&
      type != "jpeg" &&
      type != "png" &&
      type != "mp4"
    )
      return toast.error("File format not supported");
    let attachmentType;
    if (type === "jpeg" || type === "jpg" || type === "png")
      attachmentType = "image";
    else if (type === "mp4") attachmentType = "video";
    else attachmentType = "attachment";

    const formdata = new FormData();
    formdata.append("file", file);
    formdata.append("upload_preset", "qvdaphyj");
    formdata.append("cloud_name", "enthuashu");
    handleAttachClose();
    setsendingAttach(true);
    Axios({
      method: "post",
      url: "https://api.cloudinary.com/v1_1/enthuashu/image/upload",
      data: formdata,
    })
      .then((response) => {
        if (response) {
          if (response.data)
            socket.current.emit("send-msg", {
              to: currentReceiver._id,
              from: user._id,
              type: "image",
              filelink: response.data.secure_url,
              filename: file.name,
              postedByUser: {
                _id: user._id,
                name: user.name,
              },
            });
          Axios({
            method: "post",
            url: `/api/user/sendmessage`,
            data: {
              to: currentReceiver._id,
              filelink: response.data.secure_url,
              filename: file.name,
              type: "image",
              from: user._id,
              connectID: selectedChatId,
            },
          }).then((res) => {
            if (res.data.success) handleChatSelect();
            setmessageText("");
            setsendingAttach(false);
          });
        }
      })
      .catch(() => {
        toast.error("Error in upload");
        setsendingAttach(false);
      });
  };

  const handleSendMessage = async (e) => {
    e.preventDefault();
    if (
      messageText == null ||
      messageText === "" ||
      messageText.trim() === "" ||
      messageText === undefined
    ) {
      return;
    }

    setSelectedChat((prev) => [
      ...prev,
      {
        from: user._id,
        to: currentReceiver._id,
        message: messageText,
        createdAt: new Date(),
        type: "text",
      },
    ]);
    socket.current.emit("send-msg", {
      from: user._id,
      to: currentReceiver._id,
      msg: messageText,
      type: "text",
      postedByUser: {
        _id: user._id,
        name: user.name,
      },
    });
    await Axios({
      method: "post",
      url: `/api/user/sendmessage`,
      data: {
        to: currentReceiver._id,
        message: messageText,
        connectID: selectedChatId,
        type: "text",
        from: user._id,
      },
    }).then((res) => {
      if (res.data.success) handleChatSelect();
      setmessageText("");
    });
  };
  const sendVideoLink = async () => {
    // socket.current.emit("send-msg", {
    //   to: currentReceiver.id,
    //   from: user.id,
    //   message: `http://localhost:3000/videocall/${selectedChatId}`,
    //   type: "meet-link",
    // });
    setSelectedChat((prev) => [
      ...prev,
      {
        from: user._id,
        to: currentReceiver._id,
        message: `http://localhost:3000/videocall/${selectedChatId}`,
        createdAt: new Date(),
        type: "meet-link",
      },
    ]);

    socket.current.emit("send-msg", {
      to: user._id,
      from: currentReceiver._id,
      message: `http://localhost:3000/videocall/${selectedChatId}`,
      createdAt: new Date(),
      type: "meet-link",
    });
    await Axios({
      method: "post",
      url: `/api/user/sendmessage`,
      data: {
        message: `http://localhost:3000/videocall/${selectedChatId}`,
        from: user._id,
        connectID: selectedChatId,
        type: "meet-link",
        to: currentReceiver._id,
      },
    }).then(() => {
      setmessageText("");
    });
  };
  useEffect(() => {
    if (socket.current) {
      socket.current.on("msg-recieve", (data) => {
        if (data.type === "text" || data.type === "meet-link") {
          setSelectedChat((prev) => [
            ...prev,

            {
              postedByUser: data.postedByUser,
              from: data.postedByUser._id,
              to: user._id,
              message: data.msg,
              createdAt: new Date(),
              type: data.type,
            },
          ]);
        } else {
          setSelectedChat((prev) => [
            ...prev,

            {
              postedByUser: data.postedByUser,
              from: data.postedByUser._id,
              to: user._id,
              filelink: data.filelink,
              filename: data.filename,
              createdAt: new Date(),
              type: data.type,
            },
          ]);
        }
      });
    }
  }, []);
  function MessageList() {
    const [search, setSearch] = useState("");

    return (
      <div className=" mh-80 me-2">
        <hr style={{ margin: "2px -10px" }} />
        <div className="row mx-0 my-1 align-items-center recentChats01_block ">
          <div className="col-9 no-padding">
            <input
              type="text"
              className="message01_search_input"
              placeholder="Search"
              style={{ border: "none" }}
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
          </div>
          <div className="col-2" style={{ textAlign: "right" }}>
            <SearchIcon className="p_button" />
          </div>
        </div>
        <div className="recentChats01_block">
          {recentChats ? (
            recentChats.length > 0 ? (
              recentChats
                .filter((item) => {
                  if (search === "") {
                    return item;
                  } else if (
                    user.type === "doctor" &&
                    item.patient &&
                    item.patient.name
                      .toLowerCase()
                      .includes(search.toLowerCase())
                  ) {
                    return item;
                  } else if (
                    user.type === "patient" &&
                    item.doctor &&
                    item.doctor.name
                      .toLowerCase()
                      .includes(search.toLowerCase())
                  ) {
                    return item;
                  }
                })
                .map((item, i) => {
                  if (user && user.type === "doctor") {
                    return (
                      <>
                        <div
                          className={
                            item._id == chat_id
                              ? "row align-items-center bg_msg_blue dashboard_block_item mt-2 mx-0 py-2"
                              : "row align-items-center dashboard_block_item mt-2 mx-0 py-2"
                          }
                          style={{ cursor: "pointer" }}
                          key={i}
                          onClick={() => {
                            navigate(`/datadashboard?id=${item._id}`);
                            setCurrentReceiver(item.patient);
                          }}
                        >
                          <div className="width_auto no-padding col-2">
                            <Avatar
                              src={item.patient.pPic}
                              className="sq_avatar"
                              sx={{ width: 45, height: 45 }}
                            />
                          </div>
                          <div className="col-10">
                            <p className="p_small_black_bold m-0 p-0">
                              {item.patient.name}
                            </p>
                          </div>
                        </div>
                      </>
                    );
                  } else if (user && user.type === "patient") {
                    return (
                      <>
                        <div
                          className={
                            item._id == chat_id
                              ? "row align-items-center bg_msg_blue dashboard_block_item mt-2 mx-0 py-2"
                              : "row align-items-center dashboard_block_item mt-2 mx-0 py-2"
                          }
                          style={{ cursor: "pointer" }}
                          key={i}
                          onClick={() => {
                            navigate(`/datadashboard?id=${item._id}`);
                            setCurrentReceiver(item.doctor);
                          }}
                        >
                          <div className="width_auto no-padding col-2">
                            <Avatar
                              src={item.doctor.doctorPic}
                              className="sq_avatar"
                              sx={{ width: 45, height: 45 }}
                            />
                          </div>
                          <div className="col-10">
                            <p className="p_small_black_bold m-0 p-0">
                              {item.doctor.name}
                            </p>
                          </div>
                        </div>
                      </>
                    );
                  }
                })
            ) : (
              <>
                <div className="row align-items-center dashboard_block_item mt-2 mx-0 py-2">
                  <div className="width_auto no-padding col-2">
                    <Avatar
                      className="sq_avatar"
                      sx={{ width: 45, height: 45 }}
                    />
                  </div>
                  <div className="col-10">
                    <p className="p_small_black_bold m-0 p-0">No Patient 1</p>
                  </div>
                </div>
              </>
            )
          ) : (
            <>
              <div className="text-center">
                <CircularProgress />
              </div>
            </>
          )}
        </div>
      </div>
    );
  }

  function ChatBox() {
    const getTime = (date) => {
      var d = new Date(date);
      var t = d.toLocaleTimeString([], { timeStyle: "short" });
      var dd = d.toLocaleDateString("en-US", { dateStyle: "short" });
      return dd + " " + t;
    };
    const getDate2 = (date) => {
      var d = new Date(date);
      return d.toLocaleDateString("en-US", {
        year: "numeric",
        day: "numeric",
        month: "short",
      });
    };

    return (
      <div className="">
        <div className="dashboard01_block ms-1">
          {currentReceiver ? (
            <>
              <div className="row no-margin align-items-center">
                <div className="width_auto">
                  <Avatar
                    src={
                      user.type === "patient"
                        ? currentReceiver.doctorPic
                        : currentReceiver.pPic
                    }
                    className="sq_avatar"
                    alt="avatar"
                  />
                </div>
                <div className="col no-padding">
                  <h5 className="h5_medium_black m-0">
                    {`${currentReceiver.name} `}
                  </h5>
                </div>
                <div className="col-2 " style={{ textAlign: "right" }}></div>
              </div>
              <hr style={{ margin: "0.5rem -10px" }} />
            </>
          ) : null}

          <div className="chat_message_box">
            {selectedChat ? (
              <ScrollableFeed>
                {selectedChat.map((message, i) => (
                  <div key={i}>
                    {!selectedChat[i - 1] ||
                    new Date(message.createdAt).getDate() !==
                      new Date(selectedChat[i - 1].createdAt).getDate() ? (
                      <p className="text-center grey_69 font12">
                        {new Date(message.createdAt).getDate() ===
                        new Date().getDate()
                          ? "Today"
                          : getDate2(message.createdAt)}
                      </p>
                    ) : null}
                    <div
                      ref={scrollRef}
                      className={
                        user._id ==
                        (message.from?._id ? message.from._id : message.from)
                          ? "chatmessage self_message d-flex align-items-center"
                          : "chatmessage other_message  d-flex align-items-center"
                      }
                    >
                      <div className="mesage_text">
                        <div className="row_align"></div>
                        {message.type === "text" ? (
                          <>
                            <p className="no-margin">{message.message}</p>
                          </>
                        ) : message.type === "attachment" ? (
                          <>
                            <div
                              className={
                                "mdmsg d-flex align-items-center flex-row-reverse mdmsg  d-flex align-items-center"
                              }
                            >
                              <div className="message_doc">
                                <div className="message_doc-main">
                                  <div className="doc_download_main">
                                    <a
                                      href={`${message.filelink}`}
                                      target="_blank"
                                      rel="noreferrer"
                                    >
                                      <button>
                                        <FileDownloadIcon />
                                      </button>
                                    </a>
                                  </div>
                                  <div className="doc-name-type-main">
                                    <p className="docname">
                                      {message.filename}
                                    </p>
                                    <div className="time-type"></div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </>
                        ) : message.type === "image" ? (
                          <>
                            <a
                              href={`${message.filelink}`}
                              target="_blank"
                              rel="noreferrer"
                            >
                              <img
                                src={message.filelink}
                                className="image-fluid"
                                style={{
                                  maxHeight: "150px",
                                  maxWidth: "200px",
                                }}
                              />
                            </a>
                          </>
                        ) : message.type === "video" ? (
                          <>
                            <video width="360" height="240" controls>
                              <source src={message.filelink} type="video/mp4" />
                              Your browser does not support the video tag.
                            </video>
                          </>
                        ) : message.type === "meet-link" ? (
                          <>
                            <p>
                              Started a video call,{" "}
                              <a
                                href={`${message.message}`}
                                target="_blank"
                                rel="noreferrer"
                                style={{ textDecoration: "underline" }}
                              >
                                click to join
                              </a>
                            </p>
                            <p className="no-margin">
                              <a
                                href={`${message.filelink}`}
                                target="_blank"
                                rel="noreferrer"
                              >
                                <img
                                  src={message.filelink}
                                  className="image-fluid"
                                  style={{
                                    maxHeight: "150px",
                                    maxWidth: "200px",
                                  }}
                                />
                              </a>
                            </p>
                          </>
                        ) : null}
                        <p className="p_small_black_10 chat_time pt-1">
                          {getTime(message.createdAt)}
                        </p>
                      </div>
                    </div>
                  </div>
                ))}
              </ScrollableFeed>
            ) : (
              " "
            )}
          </div>

          {/* <div></div> */}
        </div>
        <form
          onSubmit={handleSendMessage}
          className="partner_chats_input_block mx-1"
        >
          <label className="message_send_label">
            <input
              placeholder="Enter text here..."
              type={"text"}
              className="chat_input"
              onChange={(e) => setmessageText(e.target.value)}
              value={messageText}
            />
            <div className="row">
              <div className="col">
                {" "}
                <Button
                  aria-describedby={id}
                  variant="contained"
                  className="message_btn send_btn"
                  onClick={sendVideoLink}
                >
                  <VideoCall />
                </Button>
              </div>
              <div className="col">
                <Button
                  aria-describedby={id}
                  variant="contained"
                  className="message_btn send_btn"
                  onClick={() => window.open("http://localhost:8000")}
                >
                  <MonitorHeartIcon />
                </Button>
              </div>
              <div className="col">
                <div className="sendFileMsgMain">
                  <button
                    className="message_btn attach_btn"
                    // onClick={() => {
                    //   showIconbar ? setShowIconBar(false) : setShowIconBar(true);
                    // }}
                    id="showIconbtn"
                  >
                    {sendingAttach ? (
                      <CircularProgress size={30} />
                    ) : (
                      <>
                        <Button
                          aria-describedby={id}
                          variant="contained"
                          className=""
                          onClick={handleClick}
                        >
                          <AttachFileIcon />
                        </Button>
                        <Popover
                          id={id}
                          open={openAttach}
                          anchorEl={anchorEl}
                          onClose={handleAttachClose}
                        >
                          <div className="message_attachment_popper">
                            <div>
                              <label htmlFor="photodoc">
                                <PhotoIcon />
                              </label>
                              <input
                                type="file"
                                accept="image/*"
                                id="photodoc"
                                name="photodoc"
                                onChange={(e) => sendAttachment(e)}
                                style={{ display: "none" }}
                              />
                            </div>
                          </div>
                        </Popover>
                      </>
                    )}
                  </button>
                </div>
              </div>
            </div>

            <button
              className="message_btn send_btn"
              onClick={(e) => {
                handleSendMessage(e);
              }}
            >
              SEND
            </button>
          </label>
        </form>
      </div>
    );
  }
  return (
    <div className="messages_root">
      <div className="row no-margin">
        <div className="col-4">{MessageList()}</div>
        <div className="col-8">
          {openChatBox && currentReceiver ? (
            ChatBox()
          ) : (
            <div className="dashboard01_block mh-80 ">
              <p className="text-center p_small_grey_bold mt-2">
                Please select a chat to start conversation{" "}
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
